import config from "@/config";

export class DB {
  private API_URL: string;
  private getAuthURL: string;
  private createPeriodURL: string;
  private getAllTokensURL: string;
  private getAllRewardAddressesURL: string;

  constructor() {
    this.API_URL = config.API.API_URL;
    this.getAuthURL = config.API.getAuth;
    this.createPeriodURL = config.API.createPeriod;
    this.getAllTokensURL = config.API.getAllTokens;
    this.getAllRewardAddressesURL = config.API.getAllRewardAddresses;
  }

  async getAllTokens() {
    //gets all tokens from the database
    const response = await fetch(this.API_URL + this.getAllTokensURL);
    const data = await response.json();
    return data;
  }

  async getAllRewardAddresses() {
    //gets all reward addresses from the database
    const response = await fetch(this.API_URL + this.getAllRewardAddressesURL);
    const data = await response.json();
    return data;
  }

  async getAuth() {
    // GET request using fetch with async/await
    const response = await fetch(this.API_URL + this.getAuthURL);
    const data = await response.json();
    return data;
  }

  async storePeriod(body: any) {
    const requestOptions = {
      method: "POST",
      body: JSON.stringify(body),
    };

    const response = await fetch(
      config.API.API_URL + this.createPeriodURL,
      requestOptions
    );
    const data = await response.json();
    return data;
  }
}
