


































































































































import Vue from "vue";
import { Wallet } from "./lib/Wallet";
import { DB } from "./lib/DB";
import { CollectionItem } from "@/lib/Interfaces";
import DiaWallet from "@/components/DiaWallet.vue";
import { MerkleTree } from "merkletreejs";
import keccak256 from "keccak256";
import { utils } from "ethers";
import {sha3_512} from "js-sha3";

export default Vue.extend({
  name: "App",
  data: () => {
    return {
      cvsContent: "0xdf0224fDDF6a6dbd1c46B04D155B3c57c1911F97,10000000000000000000\n0x2694AfC3b247D19e3D1D6345B2Aa16514Bce0c08,50000000000000000000\n0x67a691d5f88CA86a37b46b4E0848F2884CdF3A6C,80000000000000000000\n0x226dcdD341415b5030Edb718F7DbB31Fef5d2a77,120000000000000000000",
      wallet: new Wallet(),
      DB: new DB(),
      collection: undefined as CollectionItem[] | undefined,
      rewardsToken: "1",
      rewardsFrom: "0x286cfb5145b49418530eb54fb1847a76c5f1e7d3",
      periodName: "January Airdrop",
      signedAuth: "",
      selectedActive: true,
      tokenChoices: Object(),
      rewardAddressesChoices: null,
      activeChoices: [{
        key: false,
        name: "No",
      },
      {
        key: true,
        name: "Yes",
      }],
    };
  },
  components: {
    DiaWallet,
  },
  watch: {
    wallet() {
      this.walletChanged();
    },
  },
  mounted(){
    //get all token choices
    this.DB.getAllTokens().then((tokenChoices) => {
      this.tokenChoices = tokenChoices;
    });

    //get all rewardAddressesChoices 
    this.DB.getAllRewardAddresses().then((rewardAddressesChoices) => {
      this.rewardAddressesChoices = rewardAddressesChoices;
    });

  },
  methods: {
    async authenticate(){

    },
    async processMerkle() {

      //add header 
      let newContent = "address,amount\n" + this.cvsContent;
      const parsedStr = this.toJSON(newContent);

      const merkleTree = this.generateMerkle(parsedStr);
      const root = merkleTree.getHexRoot();
      const totalAmount = this.getTotalAmount(parsedStr);
      

      // get latest counter
      const counter = await this.wallet.getCounter()

      const signed = await this.signMessage("I am authing with DIA + nonce");
      const findKey = Object.keys(this.tokenChoices).find(key => this.tokenChoices[key].tokenID === this.rewardsToken);
      if(findKey){
        const rewardsTokenAddress = this.tokenChoices[findKey].address;

        // check if merkle contract has enough allowance from the rewards from address)
        const currentAllowance = await this.wallet.getMerkleContractAllowance(rewardsTokenAddress, this.rewardsFrom);
        if (currentAllowance !== undefined){
          console.log(`currentAllowance is ${currentAllowance}, totalAmount is ${totalAmount}. Allowance is enough: ${currentAllowance >= totalAmount}`);  
        }else{
          console.log("Wrong token picked");
        }
        

        await this.wallet.addPeriod(root, rewardsTokenAddress, this.rewardsFrom, this.selectedActive);
        const storePeriodBody = {
          periodName: this.periodName,
          tokenAddressID: this.rewardsToken,
          rewardsAddressID: this.rewardsFrom,
          active: this.selectedActive,
          fullAddressesAmounts: parsedStr,
          merkleTreeRoot: root,
          counter,
          signedAuth: signed
        }
        await this.DB.storePeriod(storePeriodBody);
      }
      
    },
    getTotalAmount( parsedStr: any){
      let totalAmount = 0;
      let pJSON = JSON.parse(parsedStr);
      for (let i = 0; i < pJSON.length; i++) {
        totalAmount += parseInt(pJSON[i].amount);
      }
      return totalAmount;
    },
    toJSON( csv : string ) {
        const lines = csv.split('\n')
        const result = Array()
        const headers = lines[0].split(',')

        lines.map(l => {
            const obj = Object()
            const line = l.split(',')

            headers.map((h, i) => {
                obj[h] = line[i]
            })

            result.push(obj)
        })
        result.shift();
        return JSON.stringify(result)
      },
    async signMessage(_message: string){
      //TODO get nonce here
      const msg = sha3_512(_message);
      const signedMSG = await this.wallet.signMessage(msg);
      if (signedMSG) {
        this.signedAuth = signedMSG;
      }
      return signedMSG;
    },
    generateMerkle(parsed: string) {
      const elements = JSON.parse(parsed).map((x: any, index: number) =>
        utils.solidityKeccak256(
          ["uint256", "address", "uint256"],
          [index, x.address, x.amount]
        )
      );
      const merkleTree = new MerkleTree(elements, keccak256, { sort: true });
      return merkleTree;
    },
    async walletChanged() {
    },
  },
});
