












































































import Vue from "vue";
import { Wallet } from "@/lib/Wallet";
import WalletConnectProvider from "@walletconnect/web3-provider";
import config from "@/config";
import { DB } from "@/lib/DB";
import Web3Modal, { getProviderInfo } from "web3modal";
import { WalletProvider } from "@/lib/Interfaces";

export default Vue.extend({
  name: "DiaWallet",
  props: {
    wallet: Wallet,
    variant: String,
    classes: String,
  },
  data: () => {
    return {
      loading: true,
      providerName: "" as string | undefined,
      isWalletModalVisible: false,
      browserSupported: false,
      DB: new DB(),
    };
  },
  computed: {
    connected(): boolean {
      return this.wallet.isConnected;
    },
    walletAddress(): string {
      return this.wallet.walletAddress;
    },
    networkName(): string {
      return this.wallet.networkName;
    },
    networkSupported(): boolean {
      return this.wallet.isNetworkSupported;
    },
  },
  methods: {
    getWeb3Modal() {
      const providerOptions = {
        // WalletConnect is disabled for now
        // walletconnect: {
        //   package: WalletConnectProvider,
        //   options: {
        //     infuraId: config.walletConnectOptions.infuraId,
        //   },
        // },
      };
      return new Web3Modal({
        cacheProvider: true,
        providerOptions,
      });
    },
    async connect() {
      this.isWalletModalVisible = false;
      this.loading = true;
      try {
        const provider = await this.getWeb3Modal().connect();
        await this.connectWallet(provider);
        if (!(await this.authenticateAdmin())) {
          console.log("You are not admin");
          await this.disconnect();
        } else {
          console.log("Connected as admin");
        }
      } finally {
        this.loading = false;
      }
    },
    async authenticateAdmin() {
      try {
        const authSet = (await this.DB.getAuth()).Items;

        //loop authSet to see if the current wallet is in theres
        for (let i = 0; i < authSet.length; i++) {
          if (authSet[i].id === this.walletAddress) {
            return 1;
          }
        }
      } catch (err) {
        console.log("error: ", err);
      } finally {
        this.loading = false;
      }
    },
    async connectWallet(provider: WalletProvider) {
      try {
        const wallet = await Wallet.init(provider);
        this.providerName = getProviderInfo(provider).name;

        if (!wallet.isConnected) {
          await this.disconnect();
        } else {
          provider.on!("chainChanged", async () => {
            await this.wallet.updateNetworkName();
            this.$emit("wallet-changed");
          });
          provider.on!("disconnect", this.disconnect);
          provider.on!("accountsChanged", async () => {
            await this.wallet.updateAccount();
            this.$emit("wallet-changed");
          });

          this.$emit("update:wallet", wallet);
        }
      } catch (error: any) {
        (this as any).$bvToast.toast(`${error.message}`, {
          title: "Error",
          variant: "danger",
          solid: true,
          toaster: "b-toaster-top-center",
          noAutoHide: true,
        });
      }
    },
    async disconnect() {
      await this.getWeb3Modal().clearCachedProvider();
      await this.wallet.disconnect();
      this.providerName = undefined;
      this.$emit("update:wallet", new Wallet());
    },
    async isWalletConnectConnected(): Promise<boolean> {
      const provider = new WalletConnectProvider({
        ...config.walletConnectOptions,
        qrcode: false,
      });
      return new Promise<boolean>((resolve) => {
        provider.connector.on("display_uri", () => {
          resolve(false);
        });
        provider.enable().then(() => {
          resolve(true);
        });
      });
    },
    async init() {
      if (window.ethereum || (window.web3 && window.web3.currentProvider)) {
        this.browserSupported = true;
        const provider = window.ethereum
          ? window.ethereum
          : window.web3.currentProvider;
        await this.connectWallet(provider);
      }

      this.loading = false;
    },
  },
  created() {
    this.init();
  },
});
