import Web3 from "web3";
import { Contract } from "web3-eth-contract";
import config, { NetworkConfig } from "@/config";
import { WalletProvider } from "./Interfaces";
import chains from "./chains.json";
import contracts from "../contracts";
import { urlToHttpOptions } from "url";

export class Wallet {
  private web3?: Web3;
  private _provider?: WalletProvider;
  private _chainId?: number;
  private _networkName?: string;
  private _walletAddress?: string;
  private ERC20Tokens?: Contract[];
  private MMerkleContract?: Contract;

  constructor(provider?: WalletProvider, web3?: Web3) {
    this._provider = provider;
    this.web3 = web3;
  }

  static async init(provider: WalletProvider): Promise<Wallet> {
    const web3 = new Web3(provider as any);
    const wallet = new Wallet(provider, web3);
    await wallet.updateNetworkName();
    await wallet.updateAccount();
    await wallet.initContracts();

    return wallet;
  }

  /**
   * The following methods update properties and change the
   * state of the connection. Therefore, any call must be
   * followed by a refresh in Vue, since properties in
   * this class aren't reactive.
   */

  async disconnect(): Promise<void> {
    const provider = this._provider as any;
    if (provider !== undefined && provider.disconnect !== undefined) {
      provider.disconnect();
    }
  }

  reset(): void {
    this.web3 = undefined;
  }

  async updateNetworkName(): Promise<void> {
    if (this.web3 !== undefined) {
      this._chainId = await this.web3.eth.getChainId();
      const chainConfig = chains.find(
        (chain: any) => chain.chainId === this._chainId
      );
      this._networkName = chainConfig?.name;
      await this.initContracts();
    }
  }

  async updateAccount(): Promise<void> {
    if (this.web3 !== undefined) {
      const accounts = await this.web3.eth.getAccounts();
      if (accounts.length === 0) {
        this.reset();
      }
      this._walletAddress = accounts[0];
    }
  }

  /** --- **/

  async initContracts(): Promise<void> {
    if (
      this.web3 !== undefined &&
      this._networkName !== undefined &&
      this.isNetworkSupported
    ) {
      const MMAddress =
        this.currentNetworkConfig?.multipleMerkleContractAddress;
      const ERC20Addresses =
        this.currentNetworkConfig?.allowedERC20TokenAddresses;
      if ((MMAddress === "" || MMAddress === undefined) && !this.isOnMainnet) {
        return;
      }
      this.MMerkleContract = new this.web3.eth.Contract(
        contracts.MultiMerkleDistributor.abi as any,
        MMAddress
      );
      if (ERC20Addresses !== undefined) {
        //initiate a contract for each ERC20Address
        for (const address of ERC20Addresses) {
          let ERC20Token = new this.web3.eth.Contract(
            contracts.ERC20.abi as any,
            address
          );
          if (this.ERC20Tokens !== undefined) {
            this.ERC20Tokens.push(ERC20Token);
          } else {
            this.ERC20Tokens = [ERC20Token];
          }
        }
      }
    }
  }

  get isConnected(): boolean {
    return this.web3 !== undefined;
  }

  get isNetworkSupported(): boolean {
    //support mainnet
    return (
      this._chainId === undefined ||
      config.mainnetNetworkConfig.chainId === this._chainId
    );
  }

  get isOnMainnet(): boolean {
    return config.mainnetNetworkConfig.chainId === this._chainId;
  }

  get walletAddress(): string {
    return this._walletAddress ?? "";
  }

  get networkName(): string {
    return this._networkName ?? "";
  }

  get chainExplorerPrefix(): string {
    switch (this._chainId) {
      case 1:
        return "https://etherscan.io";
      case 42161:
        return "https://arbiscan.io";
      case 421611:
        return "https://testnet.arbiscan.io";
      default:
        return `https://${this.currentNetworkConfig?.shortname}.etherscan.io`;
    }
  }

  private get currentNetworkConfig(): NetworkConfig | undefined {
    if (this.isOnMainnet) {
      return config.mainnetNetworkConfig;
    } else {
      // return config.arbitrumNetworkConfig;
    }
  }

  /*
  function addPeriod(
        bytes32 _merkleRoot,
        address _tokenAddress,
        address _rewardsFrom,
        bool _isActive
    ) public onlyOwner 
    addPeriod(_merkleRoot, _tokenAddress, _rewardsFrom, _isActive)
    external
    returns (bool)
    */
  //adds period to blockchain
  addPeriod(
    _merkleRoot: String,
    _tokenAddress: String,
    _rewardsFrom: String,
    _isActive: Boolean
  ): Promise<void> {
    if (this.MMerkleContract !== undefined) {
      return this.MMerkleContract.methods
        .addPeriod(_merkleRoot, _tokenAddress, _rewardsFrom, _isActive)
        .send({ from: this.walletAddress });
    } else {
      return Promise.reject("MMerkleContract is undefined");
    }
  }

  // get counter
  getCounter(): Promise<number> {
    if (this.MMerkleContract !== undefined) {
      return this.MMerkleContract.methods.getPeriodLength().call();
    } else {
      return Promise.reject("MMerkleContract is undefined");
    }
  }
  // check if merkle contract has enough allowance from the rewards from address
  // currentAllowance = await this.wallet.getMerkleContractAllowance(
  //   rewardsTokenAddress,
  //   this.rewardsFrom
  // );

  async getMerkleContractAllowance(_TokenAddress: String, _owner: String) {
    if (this.ERC20Tokens) {
      for (let i = 0; i < this.ERC20Tokens?.length; i++) {
        if (
          this.ERC20Tokens[i].options.address.toLowerCase() ==
          _TokenAddress.toLowerCase()
        ) {
          const token = this.ERC20Tokens[i];
          const allowance = await token.methods
            .allowance(
              _owner,
              this.currentNetworkConfig?.multipleMerkleContractAddress
            )
            .call();
          // console.log(
          //   `Owner: ${_owner} Token: ${_TokenAddress} MMerkle: ${this.currentNetworkConfig?.multipleMerkleContractAddress} Allowance: ${allowance} `
          // );
          return allowance;
        }
      }
    } else {
      return Promise.reject("ERC20Tokens is undefined");
    }
  }

  async signMessage(_message: string): Promise<string | undefined> {
    const signPassword = "test123";
    try {
      const signedMSG = await this.web3?.eth.personal.sign(
        _message,
        this.walletAddress,
        signPassword
      );
      return signedMSG;
    } catch (e) {
      return Promise.reject(e);
    }

    // if (signedMSG !== null){
    //   return signedMSG;
    // }else{
    //   return error
    // }
  }
  //switches the network to mainnet
  async switchToMainnet(): Promise<void> {
    await this.switchNetwork(config.mainnetNetworkConfig.chainId);
    await this.updateNetworkName();
  }
  async switchNetwork(chainID: number): Promise<void> {
    try {
      await this._provider?.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: this.web3?.utils.toHex(chainID) }], // chainId must be in hexadecimal numbers
      });
    } catch (e: any) {
      if (e.code == 4902) {
        // user doesn't have the chain added
        await this.addNetwork(chainID);
      }
    }
  }

  async addNetwork(chainId: number): Promise<void> {
    const chain = chains.find((chain: any) => chain.chainId === chainId);
    if (chain !== undefined) {
      const params = {
        chainId: this.web3?.utils.toHex(chain.chainId), // A 0x-prefixed hexadecimal string
        chainName: chain.name,
        nativeCurrency: {
          name: chain.nativeCurrency.name,
          symbol: chain.nativeCurrency.symbol, // 2-6 characters long
          decimals: chain.nativeCurrency.decimals,
        },
        rpcUrls: chain.rpc,
        blockExplorerUrls: [chain.infoURL],
      };
      try {
        await this._provider?.request({
          method: "wallet_addEthereumChain",
          params: [params, this._walletAddress],
        });

        //added it, now switch to it
        await this.switchNetwork(chain.chainId);
      } catch (e: any) {
        if (e.code == 4902) {
          console.log("user denied");
        }
      }
    }
  }
}
